<div class="hello"><router-link v-if="clickable" :to="'/flag/'+ (h.uid || h.idx)"><div class="image"><h3 class="ui header"><img class="main" :src="getIcon(h)" alt="^_^">{{h.name}}<span v-if="h.learner_birth">-<span v-html="toAge(h.learner_birth)"></span>歲</span><span v-if="h.child_birth">(孩子約</span><span v-if="h.child_birth2">{{toAge(h.child_birth2)}} ~</span><span v-if="h.child_birth">{{toAge(h.child_birth)}} 歲)</span></h3></div><div class="content"><p class="description gray"> <span v-html="highlight(h.address, mySearch)"></span>&nbsp;&nbsp; - {{countDateDiff(h.lastUpdate)}}{{ $t('login.alreadyUpdated') }}</p><p class="description" v-if="h.learner_habit" v-html="$t('card.interests') + highlight(h.learner_habit, mySearch)"></p><p class="description" v-if="h.share" v-html="$t('card.shareable') + highlight(h.share, mySearch)"></p><p class="description" v-if="h.ask" v-html="$t('card.seeking') + highlight(h.ask, mySearch)"></p><p class="description" v-if="h.price" v-html="$t('card.payable') + highlight(h.price, mySearch)"></p></div><div class="content" v-if="h.connect_me &amp;&amp; full"><div class="ui divider"></div><p class="description" v-if="isEmail(h.connect_me)">{{ $t('contact.clickableEmail') }}<a @click.stop="gotoMail(h.connect_me)">{{h.connect_me}}</a></p><p class="description" v-else>{{ $t('contact.contactMethod') }} {{h.connect_me}}</p><p class="description" v-if="h.freetime">{{ $t('contact.availableTime') }} {{h.freetime}}</p></div><div class="content"><div class="ui divider"></div><p v-if="h.site"><a @click.stop="goto(h.site)">{{ $t('contact.personalWebsite') }}：<img :src="'https://www.google.com/s2/favicons?domain=' + h.site" title="個人網址" alt="個人網址"><i class="right arrow icon"></i></a></p><p v-if="h.site2"><a @click.stop="goto(h.site2)">{{ $t('contact.communityWebsite') }}：<img :src="'https://www.google.com/s2/favicons?domain=' + h.site2" title="社群網址" alt="個人網址"><i class="right arrow icon"></i></a></p></div><div class="content"><div class="ui divider"></div><p class="description" v-if="!full"><span class="text" v-html="highlight(part(h.note), mySearch) + '...'"></span><br></p><p class="description" v-else><span class="text" v-html="highlight(h.note, mySearch)"></span><br></p></div></router-link><div v-else :to="'/flag/'+ (h.uid || h.idx)"><div class="image"><h3 class="ui header"><img class="main" :src="getIcon(h)" alt="^_^">{{h.name}}<span v-if="h.learner_birth">-<span v-html="toAge(h.learner_birth)"></span>歲</span><span v-if="h.child_birth">(孩子約</span><span v-if="h.child_birth2">{{toAge(h.child_birth2)}} ~</span><span v-if="h.child_birth">{{toAge(h.child_birth)}} 歲)</span></h3></div><div class="content"><p class="description gray"> <span v-html="highlight(h.address, mySearch)"></span>&nbsp;&nbsp; - {{countDateDiff(h.lastUpdate)}}{{ $t('login.alreadyUpdated') }}</p><p class="description" v-if="h.learner_habit" v-html="$t('card.interests') + highlight(h.learner_habit, mySearch)"></p><p class="description" v-if="h.share" v-html="$t('card.shareable') + highlight(h.share, mySearch)"></p><p class="description" v-if="h.ask" v-html="$t('card.seeking') + highlight(h.ask, mySearch)"></p><p class="description" v-if="h.price" v-html="$t('card.payable') + highlight(h.price, mySearch)"></p></div><div class="content" v-if="h.connect_me &amp;&amp; full"><div class="ui divider"></div><p class="description" v-if="isEmail(h.connect_me)">{{ $t('contact.clickableEmail') }}<a @click.stop="gotoMail(h.connect_me)">{{h.connect_me}}</a></p><p class="description" v-else>{{ $t('contact.contactMethod') }} {{h.connect_me}}</p><p class="description" v-if="h.freetime">{{ $t('contact.availableTime') }}： {{h.freetime}}</p></div><div class="content"><div class="ui divider"></div><p v-if="h.site"><a @click.stop="goto(h.site)">{{ $t('contact.personalWebsite') }}：<img :src="'https://www.google.com/s2/favicons?domain=' + h.site" title="個人網址" alt="個人網址"><span class="fat-only">{{h.site}}</span><i class="right arrow icon"></i></a></p><p v-if="h.site2"><a @click.stop="goto(h.site2)">{{ $t('contact.communityWebsite') }}：<img :src="'https://www.google.com/s2/favicons?domain=' + h.site2" title="社群網址" alt="個人網址"><span class="fat-only">{{h.site2}}</span><i class="right arrow icon"></i></a></p></div><div class="content"><div class="ui divider"></div><p class="description" v-if="!full"><span class="text" v-html="highlight(part(h.note), mySearch) + '...'"></span><br></p><p class="description" v-else><span class="text" v-html="highlight(h.note, mySearch)"></span><br></p></div></div><div class="filler"></div><div class="ui bottom attached stackable buttons" :class="{vertical: !full || thin}"><div class="ui green button" @click="addBook(h.uid || h.idx)" v-if="uid &amp;&amp; (!book || book.indexOf(h.uid || h.idx) == -1)"><i class="book icon"></i>{{ $t('buttons.addToDirectory') }}</div><div class="ui red button" @click="removeBook(book.indexOf(h.uid || h.idx))" v-else-if="uid &amp;&amp; book &amp;&amp; book.indexOf(h.uid || h.idx) &gt; -1"><i class="book icon"></i>{{ $t('buttons.removeFromDirectory') }}</div><div class="ui purple button" @click="locate(h, true)"><i class="map icon"></i>{{ $t('buttons.viewMap') }}</div><div class="ui blue button" @click="copyFlagURL(h.uid)"><i class="share square icon"></i>{{ $t('buttons.shareCard') }}</div><router-link class="ui teal button" :to="'/qr/' + h.uid"><i class="qrcode icon"></i>{{ $t('buttons.showQRCode') }}</router-link></div></div>